
const defaultDuration = 1000*60*60;//默认一小时

//写cookies（设置作用域）
function setCookieWithScope(name,value,expireTime=defaultDuration){
    var exp = new Date();
    exp.setTime(exp.getTime() + expireTime||defaultDuration);
    let hostname = location.hostname;
    document.cookie = name + "="+ escape (value) + ";expires=" + exp.toUTCString()+";domain="+hostname+";path=/";
}



//读取cookies
function getCookie(name) {
    var arr,reg=new RegExp("(^| )"+name+"=([^;]*)(;|$)");
    if(arr=document.cookie.match(reg))
        return (arr[2]);
    else
        return null;
}

//删除cookies
function delCookie(name)
{
    var exp = new Date();
    exp.setTime(exp.getTime() - 1);
    var cval=getCookie(name);
    if(cval!=null)
        document.cookie= name + "="+cval+";expires="+exp.toGMTString();
}
//删除cookies（有作用域的）
function delCookieWithScope(name){
    var exp = new Date();
    exp.setTime(exp.getTime() - 1);
    var cval=getCookie(name);
    if(cval!=null){
        let hostname = location.hostname.substring(location.hostname.indexOf(".")+1)
        document.cookie= name + "='';expires="+exp.toGMTString()+";domain="+hostname+";path=/";
    }
}

module.exports = {
    setCookieWithScope,
    getCookie,
    delCookie,
    delCookieWithScope
}
