/**
 * 判断传入数据是否为空
 * @param val
 * @returns {*}
 */

function isEmpty(val){
    switch(Object.prototype.toString.call(val)){
        case '[object Array]':
            return !(val&&val.length);
        case '[object Object]':
            return !(val&&JSON.stringify(val)!=='{}');
        case '[object Null]':
        case '[object Undefined]':
            return true;
        case '[object Boolean]':
            return val
        case '[object String]':
            return !val;
        default:
            return false;
    }
}
function redirectToLogin(){
    if(location.pathname!=='/login'){
        // localtion.href = 'login' 多级目录时会出Bug，改为根目录
        location.href = '/login';
    }
}

module.exports = {
    isEmpty,
    redirectToLogin
}
